.about-block {
	position: relative;
	width: 100%;
	height: 324px;
	background: url("../images/images/back-condition.svg") no-repeat center / contain;
}

.about-block__title {
	display: inline-block;
	margin: 75px 0 0 65px;
	font-weight: 500;
	font-size: 26px;
	text-transform: uppercase;
	color: #fff;
}

.about-block__text {
	margin: 40px 0 0 65px;
	max-width: 740px;
	font-size: 16px;
	line-height: 1.4;
	letter-spacing: .5px;
	color: #fff;
}